import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import {
    Form,
    FormGroup,
    FormText,
    Input,
    Label,
    Col,
    Collapse,
    Button,
    Card,
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    Nav,
    NavItem,
    NavLink,
    TabContent,
    TabPane,
    Row,
    Container,
} from "reactstrap";
import "./Profile.css";
import userApi from "api/user/userApi";
import AvatarEdit from "react-avatar-edit";
import defaultAvatar from "assets/images/default-avatar.png";

const AVATAR_MAXSIZE = 500000;

export default function Profile(props) {
    const { t } = useTranslation();
    const { isLogined } = props;

    const [activeTab, setActiveTab] = useState("1");
    const [user, setUser] = useState({ name: "", password: "" });

    const [imageSource, setImageSource] = useState(null);
    const [avatar, setAvatar] = useState(null);

    const [isOpen1, setIsOpen1] = useState(false);
    const toggle = (tab) => {
        if (activeTab !== tab) {
            setActiveTab(tab);
        }
    };

    const toggle1 = () => setIsOpen1(!isOpen1);

    const handleChangeInfomation = () => {
        return 0;
    };
    const handleUploadAvatar = async () => {
        alert("upload avatar");
    };
    const onClose = () => {
        setAvatar(null);
    };

    const onCrop = (event) => {
        setAvatar(event);
    };

    const onBeforeFileLoad = (elem) => {
        if (elem.target.files[0].size > AVATAR_MAXSIZE) {
            alert(t("AVATAR_MAXSIZE_WARNING"));
            elem.target.value = "";
        }
    };

    return (
        <div>
            <Nav tabs>
                <NavItem>
                    <NavLink
                        className={activeTab === "1" ? "active" : ""}
                        onClick={() => {
                            toggle("1");
                        }}
                    >
                        <Row className="mt-3">Profile</Row>
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={activeTab === "2" ? "active" : ""}
                        onClick={() => {
                            toggle("2");
                        }}
                    >
                        <Row className="mt-3">Edit Profile</Row>
                    </NavLink>
                </NavItem>
            </Nav>
            <Container className="mt-4">
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">
                        <Card className="cardT shadow">
                            <Container className="my-3">
                                <Row>
                                    <Col lg={4} className="text-center">
                                        <img
                                            src={
                                                isLogined.avatar
                                                    ? isLogined.avatar
                                                    : defaultAvatar
                                            }
                                            className="avatar"
                                            width="200px"
                                            height="200px"
                                            alt="Avatar"
                                        />
                                    </Col>
                                    <Col lg={3}>
                                        <Row className="mt-4">
                                            <Col lg={12}>
                                                <h4>
                                                    {isLogined.firstName +
                                                        " " +
                                                        isLogined.lastName}
                                                </h4>
                                            </Col>
                                            <Col lg={12}>
                                                <Label>{isLogined.email}</Label>
                                            </Col>
                                            <Col lg={12}>
                                                <Label for="role">
                                                    {isLogined.role.name}
                                                </Label>
                                            </Col>
                                        </Row>
                                    </Col>
                                    <Col lg={5} className="mt-4">
                                        <a
                                            className="typeLink"
                                            href="/update-profile"
                                        >
                                            {t("CHANGEPROFILE")}
                                        </a>
                                    </Col>
                                </Row>
                            </Container>
                        </Card>
                    </TabPane>
                    <TabPane tabId="2">
                        <Card className="cardT shadow">
                            <Button
                                color="primary"
                                onClick={toggle1}
                                style={{ marginBottom: "1rem" }}
                            >
                                Edit1
                            </Button>
                            <Collapse isOpen={isOpen1}>
                                <Row>
                                    <Col xs={6}>
                                        <AvatarEdit
                                            width={380}
                                            height={295}
                                            onCrop={onCrop}
                                            onClose={onClose}
                                            onBeforeFileLoad={onBeforeFileLoad}
                                            src={imageSource}
                                        />
                                    </Col>
                                    <Col xs={6}>
                                        {avatar ? (
                                            <img src={avatar} alt="Xem trước" />
                                        ) : (
                                            <span>Thêm ảnh để xem trước</span>
                                        )}
                                    </Col>
                                </Row>
                                <form
                                    method="form"
                                    onSubmit={handleUploadAvatar}
                                >
                                    <Button
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                    >
                                        Đổi ảnh đại diện
                                    </Button>
                                </form>
                            </Collapse>
                        </Card>
                    </TabPane>
                </TabContent>
            </Container>
        </div>
    );
}
